import React from 'react';
import classes from './Reviews.module.css'
import userLogo from '../../img/user.jpg'

const Reviews = props => {
    return (
        <div>
            <p className={classes.rewTitle}>Reviews</p>
            <div className={classes.rewWrapper}>
                <div className={classes.hide}>
                {
                    props.reviewsInfo.list ? 
                    props.reviewsInfo.list.map((review, index) => {
                        return review.author_details.avatar_path === null ? 
                        <section key={index}>
                            <img className={classes.avatar} src={userLogo} alt=""/>
                            {
                            review.author_details.rating ?
                            <p className={classes.userName}>{review.author} &nbsp; <i className="fas fa-star"></i> <strong className={classes.rating}>{review.author_details.rating}</strong></p>
                             : 
                             <p className={classes.userName}>{review.author}</p>
                             }
                            
                            <p className={classes.content}>{review.content}</p>
                            <p className={classes.contentTime}>{review.created_at.slice(0, 10)} &nbsp; {review.created_at.slice(11, 16)}</p>
                        </section>
                        : review.author_details.avatar_path.includes("gravatar.com") ?
                        <section key={index}>
                            <img className={classes.avatar} src={review.author_details.avatar_path.slice(1)} alt=""/>
                            {
                            review.author_details.rating ?
                            <p className={classes.userName}>{review.author} &nbsp; <i className="fas fa-star"></i> <strong className={classes.rating}>{review.author_details.rating}</strong></p>
                             : 
                             <p className={classes.userName}>{review.author}</p>
                             }
                            <p className={classes.content}>{review.content}</p>
                            <p className={classes.contentTime}>{review.created_at.slice(0, 10)} &nbsp; {review.created_at.slice(11, 16)}</p>
                        </section>
                        :
                        <section key={index}>
                            <img className={classes.avatar} src={"https://image.tmdb.org/t/p/w500/" + review.author_details.avatar_path} alt=""/>
                            {
                            review.author_details.rating ?
                            <p className={classes.userName}>{review.author} &nbsp; <i className="fas fa-star"></i> <strong className={classes.rating}>{review.author_details.rating}</strong></p>
                             : 
                             <p className={classes.userName}>{review.author}</p>
                             }
                            <p className={classes.content}>{review.content}</p>
                            <p className={classes.contentTime}>{review.created_at.slice(0, 10)} &nbsp; {review.created_at.slice(11, 16)}</p>
                        </section>
                    }) : null
                }
                </div>
            </div>
        </div>
    )
}

export default Reviews