import React, { Component } from 'react'
import classes from './MovieInfo.module.css'
import Reviews from '../Reviews/Reviews'

class MovieInfo extends Component{

    componentWillMount() {
        this.props.getMovieInfo()
        this.props.getVideo()
        this.props.getReviews()
    }

    render() {
        const cls = [classes.close, "fas", "fa-times"]
        const Genres = []
        const Production = []


        if(this.props.movieAbout.production) {
            for(let company of this.props.movieAbout.production) {
                Production.push(company.name)
            }
        }

        if(this.props.movieAbout.genres) {
            for(let genre of this.props.movieAbout.genres) {
                Genres.push(genre.name)
            }
        }
        

        if(this.props.movieAbout.genres) {
            return (
                <div className={classes.MovieInfo}>
                    <div className={classes.wrapper}>
                        <div className={classes.firstBlock}>
                            <div className={classes.posterWrapper}>
                                <img className={classes.poster} src={"https://image.tmdb.org/t/p/w500/" + this.props.movieAbout.poster} alt=""/>
                                <a  target="_blank" href={this.props.movieUrl}>Watch Trailer</a>
                            </div>
                            <div className={classes.infoWrapper}>
                                <i onClick={this.props.renderMoveInfo} className={cls.join(" ")}></i>
                                <h1>{this.props.movieAbout.title}</h1>
                                <p className={classes.ganre}>{Genres.join(", ")}</p>
                                <p className={classes.reslease}>{this.props.movieAbout.releaseDate.split("-").join("/")} &nbsp; - &nbsp; {this.props.movieAbout.runtime}m</p>
                                <p className={classes.rate}><i className="fas fa-star"></i> {this.props.movieAbout.voteAverage}</p>
                                <p className={classes.overview}>{this.props.movieAbout.overview}</p>
                                <div className={classes.production}>
                                    <strong className={classes.word}>Prodoction:</strong> &nbsp;
                                    {Production.join(", ")}
                                </div>
                                {this.props.reviewRender ? <Reviews reviewsInfo={this.props.reviewsInfo}/> : <p className={classes.errMas}>No reviews found</p>}
                                
                            </div>
                        </div>
                    </div>
                </div>
            )
        }
        return null
    }

}
 
export default MovieInfo;