import React, { Component } from 'react'
import classes from './App.css';
import Navbar from './Components/Navbar/Navbar'
import MovieList from './Components/MovieList/MovieList'
import axios from 'axios'
import MovieInfo from './Components/MovieInfo/MovieInfo'

class App extends Component {
  state = {
    searchName: "",
    allPages: 0,
    activePages: 0,
    movieList: [],
    url: {
      search: "https://api.themoviedb.org/3/search/movie?api_key=c3a9ed1f4ce1866fbfc09f806bf3beb5&query=",
      topRated: "https://api.themoviedb.org/3/movie/top_rated?api_key=c3a9ed1f4ce1866fbfc09f806bf3beb5",
      popular: "https://api.themoviedb.org/3/movie/popular?api_key=c3a9ed1f4ce1866fbfc09f806bf3beb5",
      upcoming: "https://api.themoviedb.org/3/movie/upcoming?api_key=c3a9ed1f4ce1866fbfc09f806bf3beb5&region=UA",
    },
    activeUrl: "",
    searchInfo: {
      searched: false,
    },
    infoRender: false,
    fixScreen: false,
    movieAbout: {},
    activeId: '',
    movieUrl: "",
    reviews: {},
    reviewRender: true,
  }

  getSearchNameHendler = e => {
    this.setState({
      searchName: e.target.value
    })
  }

  movieRequestHendler = async (value, e, url = null) => {
    e.preventDefault()
    let requestUrl

    if(url) {
      requestUrl = url
    } else {
      requestUrl = this.state.url.search + value.toLowerCase()
    }

    try{
      const response = await axios.get(requestUrl)
      let sortData = []

      for(let movie of response.data.results) {
        if("https://image.tmdb.org/t/p/w400/" + movie.poster_path !== "https://image.tmdb.org/t/p/w400/null" &&
        movie.release_date) {
          sortData.push(movie)
        }
      }

      this.setState({
        movieList: sortData,
        allPages: response.data.total_pages,
        activePages: response.data.page,
        requestName: this.state.searchName,
        searchName: "",
        searchInfo: {searched: true,
                     requestName: value},
        activeUrl: url,
      })
    }
    catch(err) {
      console.log(err)
    }
  }

  loadMoreHendler = async (value = null, url = null) => {
    let requestUrl

    if(url) {
      requestUrl = url + "&page=" + (this.state.activePages + 1)
    } else {
      requestUrl = this.state.url.search + value.toLowerCase() + "&page=" + (this.state.activePages + 1)
    }

    try{
      const response = await axios.get(requestUrl)
      let copyList = [...this.state.movieList]

      for(let movie of response.data.results) {
        if("https://image.tmdb.org/t/p/w400/" + movie.poster_path !== "https://image.tmdb.org/t/p/w400/null" &&
        movie.release_date) {
          copyList.push(movie)
        }
      }
     
      this.setState({
        movieList: copyList,
        activePages: this.state.activePages + 1
      });
    }
    catch(err) {
      console.log(err)
    }
  }

  renderMoveInfo = (id) => {
    if(this.state.movieAbout) {
      this.setState({
        infoRender : !this.state.infoRender,
        fixScreen: !this.state.fixScreen,
        activeId: id,
        movieAbout: {}
      })
    }
    this.setState({
      infoRender : !this.state.infoRender,
      fixScreen: !this.state.fixScreen,
      activeId: id,
    })
  }
  
  getMovieInfo = async () => {
    try{
      const response = await axios.get(`https://api.themoviedb.org/3/movie/${this.state.activeId}?api_key=c3a9ed1f4ce1866fbfc09f806bf3beb5`)

      const movieAbout = {
        overview: response.data.overview,
        genres: response.data.genres,
        production: response.data.production_companies,
        title: response.data.title,
        poster: response.data.poster_path,
        releaseDate: response.data.release_date,
        runtime: response.data.runtime,
        voteAverage: response.data.vote_average
      }
      this.setState({movieAbout}); 
    }
    catch(err) {
      console.log(err)
    }
  }

  getReviews = async () => {
    try{
      const response = await axios.get(`https://api.themoviedb.org/3/movie/${this.state.activeId}/reviews?api_key=c3a9ed1f4ce1866fbfc09f806bf3beb5`)

      if(response.data.results.length == 0) {
        this.setState({reviewRender: false});
      } else {
        const reviews = {
          activePage: response.data.page,
          totalPages: response.data.total_pages,
          list: response.data.results,
        }
        this.setState({reviews, reviewRender: true});
      }
    }
    catch(err) {
      console.log(err)
    }
  }

  getVideo = async () => {
    try{
      const response = await axios.get(`https://api.themoviedb.org/3/movie/${this.state.activeId}/videos?api_key=c3a9ed1f4ce1866fbfc09f806bf3beb5`)
      const videoUrl = "https://www.youtube.com/watch?v=" + response.data.results[0].key

      this.setState({movieUrl: videoUrl})
    }
    catch(err) {
      console.log(err)
    }
  }


  render() {
    if(this.state.fixScreen) {
      document.querySelector("body").classList.add("fixed")
      document.querySelector("html").classList.add("fixed")
    } else {
      document.querySelector("body").classList.remove("fixed")
      document.querySelector("html").classList.remove("fixed")
    }
    console.log(this.state.reviews.list)
    return (
      <div>
        <Navbar
        getSearchNameHendler={this.getSearchNameHendler}
        movieRequestHendler={(e) => this.movieRequestHendler(this.state.searchName, e)}
        searchName={this.state.searchName}
        popularRequestHendler={(e) => this.movieRequestHendler(this.state.searchName, e, this.state.url.popular)}
        upcomingRequestHendler={(e) => this.movieRequestHendler(this.state.searchName, e, this.state.url.upcoming)}
        topRateRequestHendler={(e) => this.movieRequestHendler(this.state.searchName, e, this.state.url.topRated)}
        />
        <MovieList 
        list={this.state.movieList}
        searched={this.state.searchInfo.searched}
        allPages={this.state.allPages}
        activePages={this.state.activePages}
        loadMoreHendler={() => this.loadMoreHendler(this.state.searchInfo.requestName, this.state.activeUrl)}
        renderMoveInfo={this.renderMoveInfo}
        />
        {
        this.state.infoRender ? 
          <MovieInfo
          renderMoveInfo={this.renderMoveInfo} 
          getMovieInfo={this.getMovieInfo}
          movieAbout={this.state.movieAbout}
          getVideo={this.getVideo}
          movieUrl={this.state.movieUrl}
          getReviews={this.getReviews}
          reviewsInfo={this.state.reviews}
          reviewRender={this.state.reviewRender}
          /> : null
        }
      </div>

    );
  }
}
export default App;