import React from 'react'
import classes from './Navbar.module.css';
import Searchbar from '../Searchbar/Searchbar'
import logo from '../../img/logo.png'

const Navbar = (props) => {
    return(
        <div className={classes.Navbar}>
            <div className={classes.wrapper}>
                <img src={logo} className={classes.logo} alt=""/>
                <ul>
                    <li onClick={props.popularRequestHendler}>Popular</li>
                    <li onClick={props.topRateRequestHendler}>Top Rated</li>
                    <li onClick={props.upcomingRequestHendler}>Upcoming</li>
                </ul>
                <Searchbar
                getSearchNameHendler={props.getSearchNameHendler}
                movieRequestHendler={props.movieRequestHendler}
                searchName={props.searchName}
                />
            </div>
        </div>
    )
}

export default Navbar